import express from 'express';
const router = express.Router();
import { fullUpdateDB_MW, fetchAllEventOdds_MW, buildSportLeagueTable_MW, fullResultUpdateDB_MW } from '../../middleware/updateDB'

import syncCurrentStatus from './ctrls/syncCurrentStatus'
import fetchAccount from './ctrls/fetchAccount'
import fetchOpenBet from './ctrls/fetchOpenBet'
import fetchHistoryBet from './ctrls/fetchHistoryBet'
import fetchTransaction from './ctrls/fetchTransaction'
import fetchPlayerStatus from './ctrls/fetchPlayerStatus'
import editPlayerSetting from './ctrls/editPlayerSetting'
import purchaseCredit from './ctrls/purchaseCredit'
import checkNewPlayerUsername from './ctrls/checkNewPlayerUsername'
import createNewPlayer from './ctrls/createNewPlayer'
import agentAccountSetting from './ctrls/agentAccountSetting'
import approveCancelOpenBet from './ctrls/approveCancelOpenBet'

router.route('/sync-current-status').get(fullResultUpdateDB_MW, syncCurrentStatus)
router.route('/fetch-account').get(fullResultUpdateDB_MW, fetchAccount)
router.route('/fetch-open-bet').get(fullResultUpdateDB_MW, fetchOpenBet)
router.route('/fetch-history-bet').post(fullResultUpdateDB_MW, fetchHistoryBet)
router.route('/fetch-transaction').post(fetchTransaction)
router.route('/fetch-player-status').post(fullResultUpdateDB_MW, fetchPlayerStatus)
router.route('/edit-player-setting').post(editPlayerSetting)
router.route('/check-new-player-username').post(checkNewPlayerUsername)
router.route('/purchase-credit').post(purchaseCredit)
router.route('/create-new-player').post(createNewPlayer)
router.route('/agent-account-setting').post(agentAccountSetting)
router.route('/approve-cancel-open-bet').post(approveCancelOpenBet)


export default router